import React from 'react'
import './styles.css'
import { logout } from "../../store/actions/auth";
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Button from 'react-bootstrap/Button'
import { CgProfile } from 'react-icons/cg'
import { FaRegAddressBook } from 'react-icons/fa'
import { MdRateReview } from 'react-icons/md'
import axios from 'axios'
import { localhost } from "../../constant"
import Loading from '../../component/element/loading/Loading'

class Account extends React.Component {

  state = {
    user: [],
    loading: false,
  } 

  componentDidMount(){
    window.scrollTo(0,0);
    axios.get(`${localhost}/auth/users/me/`)
    .then(res => {
      this.setState({ user: res.data, loading: true })
    })
  }
  refreshPage(){
    window.location.reload()
  }
  render(){
        return (
            <div className='show-profile'>
              {!this.state.loading ? (
                <Loading />
                ): (
                  <div>
                  <h2>Welcome back {this.state.user.first_name}</h2>
                  <div className='parsonal-info'>
                   <div className='info-box'>
                   <h4><CgProfile />Personal Information</h4>
                   <p>{this.state.user.first_name}</p>
                   </div>
                   <div className='info-box'>
                   <h4><FaRegAddressBook />Email Address</h4>
                   <p>{this.state.user.email}</p>
                   </div>
                   <div className='info-box'>
                   <h4><MdRateReview />Mobile Number</h4>
                   <p></p>
                   </div>
               </div>
               {
                    this.props.isAuthenticated ?
                    <Button variant="outline-danger" size='sm' onClick={this.props.logout}>signout</Button>
                        :
                    <li><Link to="/login">login</Link></li>
                }
                </div>
                )}
            </div>
        )
  }  
}
const mapStateToProps = state => {
    return {
      isAuthenticated: state.auth.isAuthenticated
    }
  }
const mapDispatchToProps = dispatch => {
    return {
      logout: () => dispatch(logout())
    }
  }

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Account))
