import React from 'react';
import {
    withGoogleMap,
    withScriptjs,
    GoogleMap,
  } from "react-google-maps";

  function Map(){
      return(
          <GoogleMap
          defaultZoom={10}
          defaultCenter={{ lat: 12.905189, lng: 77.559898 }} />
      )
  }

  const Wrappedmap = withScriptjs(withGoogleMap(Map))

  export default function Googlediv(){
      return(
        <div style={{ width: "100vw", height: "100vh" }}>
        <Wrappedmap
          googleMapURL={`https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=AIzaSyBEYexPzzJJtOeL2F_L_DTPwnEUglK95T4`}
          loadingElement={<div style={{ height: `100%` }} />}
          containerElement={<div style={{ height: `100%` }} />}
          mapElement={<div style={{ height: `100%` }} />}
        />
      </div>
      )
  }



