import React from 'react'
import './styles.css'
import { Redirect } from 'react-router-dom'
import { connect } from 'react-redux';
import Tab from 'react-bootstrap/Tab'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Nav from 'react-bootstrap/Nav'
import Account from '../../component/customer/Account'
import Orderlist from '../../component/customer/Orderlist'
import Shipping from '../../component/customer/Shipping'
import Header from '../../component/layout/Header'
import Footer from '../../component/layout/Footer/Footer'

const Profile = props => {

    if (!props.isAuthenticated) {
        return <Redirect to='/' />
    }
        return (
            <div>
              <Header />
              <div className='show-profile-first'>
              <div className='show-profile-second'>
                <h1>MY RIGGED ACCOUNT</h1>
                </div>
              </div>
              <Tab.Container id="left-tabs-example" defaultActiveKey="first">
                <Row className='wrapper'>
                    <Col sm={12} lg={3}>
                    <Nav className="profile-tab-nav">
                        <Nav.Item className='profile-tab-item'>
                        <Nav.Link className='profile-tab-link' eventKey="first">Profile</Nav.Link>
                        </Nav.Item>
                        <Nav.Item className='profile-tab-item'>
                        <Nav.Link className='profile-tab-link' eventKey="second">Address</Nav.Link>
                        </Nav.Item>
                        <Nav.Item className='profile-tab-item'>
                        <Nav.Link className='profile-tab-link' eventKey="third">Orders</Nav.Link>
                        </Nav.Item>
                    </Nav>
                    </Col>
                    <Col sm={12} lg={9}>
                    <Tab.Content>
                        <Tab.Pane eventKey="first">
                        <Account />
                        </Tab.Pane>
                        <Tab.Pane eventKey="second">
                        <Shipping />
                        </Tab.Pane>
                        <Tab.Pane eventKey="third">
                        <Orderlist />
                        </Tab.Pane>
                    </Tab.Content>
                    </Col>
                </Row>
                </Tab.Container>
              <Footer />
            </div>
        )
}

const mapStateToProps = (state) => {
    return {
        isAuthenticated: state.auth.isAuthenticated,
        loading: state.loading,
        error: state.error
    }
  }

export default connect(mapStateToProps)(Profile);