import React from 'react'
import './styles.css'
import { Link, Redirect } from 'react-router-dom'
import {connect} from 'react-redux'
import { Button } from 'react-bootstrap'

import Header from '../../component/layout/Header'
import Footer from '../../component/layout/Footer/Footer'
import Cpu from '../../component/computercomp/Cpu'




class Pccomponents extends React.Component {
  state = {
    detailVisible: false,
}
componentDidMount(){
  window.scrollTo(0,0);
}

handleToggleForm = () => {
  const {  detailVisible } = this.state
  this.setState({
      detailVisible: !detailVisible
  })
}
  render(){
    const {isAuthenticated} = this.props
    if (!isAuthenticated) {
      return <Redirect to='/login' />
    }
    return (
        <div style={{ backgroundColor: '#1F1D29'}}>
            <Header />
            <div className='pc-components-head'>
              <div className='pc-components-head1'>
              <h1>THE H SERIES, OUR VISION FOR A MODERN PC</h1>
              <button className='vg2button'>LEARN MORE</button>
              </div>
            </div>

             <div className='pc-components2'>
             <Button variant="danger" className='pc-components-button' onClick={this.handleToggleForm}>^CATEGORIES</Button>
             {this.state.detailVisible && (
               <div className='component-tab-nav'>
                 <Link to='/graphic-card' className='component-tab-link'>GPU</Link>
                 <Link to='/memory' className='component-tab-link'>MEMORY</Link>
                 <Link to='/mother-board' className='component-tab-link'>MOTHERBOARD</Link>
               </div>
             )}
            </div>
            <Cpu />
            <Footer />
        </div>
    )
  }
}
const mapStateToProps = state => {
  return {
    isAuthenticated: state.auth.isAuthenticated
  }
}

export default connect(mapStateToProps)(Pccomponents)
